@import url('../../../variable.scss');

.follow-us-section {
    /* UI Properties */
    background: var(--primary-d1) 0% 0% no-repeat padding-box;
    background: #FF8243 0% 0% no-repeat padding-box;
    border: 1px solid #D1D3D6;
    border-radius: 2px;
    opacity: 1;
    width: 100%;
    height: 350px;
    padding: 19%;
}

.follow-us-heading {
    /* UI Properties */
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-36)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--white);
    text-align: center;
    font: normal normal 600 34px/45px Source Serif Pro;
    letter-spacing: 0px;
    color: #FFFFFF;
    opacity: 1;
}

.s-icon-sec {
    margin: 0 auto;
    display: block;
    text-align: center;
    letter-spacing: 19px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: 12px;
}

@media all and (min-width: 769px){
    .follow-us-section {
        height: auto;
    }
}