@import url('../../../variable.scss');

//Mobile View
@media only screen and (min-width: 320px) and (max-width: 768px){
    .order-heading-section h1{
        /* UI Properties */
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-48)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
    letter-spacing: var(--unnamed-character-spacing-0);
    color: var(--unnamed-color-172026);
    text-align: center;
    font: normal normal 600 36px/45px Source Serif Pro;
    letter-spacing: 0px;
    color: #172026;
    opacity: 1;
    }
    
    .hr-divider-order{
    /* UI Properties */
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    background: #E26A2C 0% 0% no-repeat padding-box;
    opacity: 1;
    height: 8px;
    width: 30%;
    margin: 0 auto;
    margin-top: 10px;
    }
    .order-heading-section{
        margin-bottom: 20px;
    }
    .follow-us-section{display: none;}
    .order-add-product{margin: 3% 5%;}
    .order-number{margin: 2% 5%;}
}

//Tablet View
@media only screen and (min-width: 769px) and (max-width: 1200px) {
    .order-summary-grid-column {
        display: grid;
        gap: 1em;
        grid-template-columns: 2fr 1fr;
    }

    .order-summary-one {
        display: grid;
        gap: 1em;
        grid-template-columns: 2fr;
        width: 109%;
         margin-left: -5%;
    }

    .order-add-product {
        grid-column: 1/-1;
        grid-row: 2/3;
    }

    .para-order-one,
    .para-order-two {
        /* UI Properties */
        font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-normal) var(--unnamed-font-size-16)/var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        text-align: left;
        font: normal normal normal 16px/24px Muli;
        letter-spacing: 0px;
        color: #172026;
        opacity: 1;
    }

    .order-number {
        grid-column: 1/-1;
        grid-row: 1/2;
    }

    .order-heading-section h1{
           /* UI Properties */
font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600) var(--unnamed-font-size-48)/var(--unnamed-line-spacing-45) var(--unnamed-font-family-source-serif-pro);
letter-spacing: var(--unnamed-character-spacing-0);
color: var(--unnamed-color-172026);
text-align: center;
font: normal normal 600 48px/45px Source Serif Pro;
letter-spacing: 0px;
color: #172026;
opacity: 1;
    }

.hr-divider-order{
    /* UI Properties */
background: var(--primary-brand) 0% 0% no-repeat padding-box;
background: #E26A2C 0% 0% no-repeat padding-box;
opacity: 1;
height: 8px;
width: 10%;
margin: 0 auto;
margin-top: 10px;
}
.order-heading-section{
    margin: 3% 0;
}
.follow-us-section{display: block;}

.main-order-summary-container{
    padding: 0 5%;
}
}

